import React, { useEffect, useState, useRef } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { toast } from 'react-toastify'

import axios from 'utils/axios'

export default function NewsForm()
{
    /** form    state */
    const [ formData, setFormData ] = useState({})
    /** author    state */
    const [ authors, setAuthors ] = useState([])
    /**    state */
    const [ categories, setCategories ] = useState([])
    /** update      newsId  */
    const { videoId } = useParams()

    /**     */
    const navigate = useNavigate()

    /** author     */
    const getAuthors = async () =>
    {
        const { success, data, error, info } = await axios.get('/api/author/')
        /**    author  state -   */
        if (success)
        {
            setAuthors(data)
        }
        else {
            /**      */
            toast(error)
        }
    }

    /**    */
    const getCategories = async () =>
    {
        const { success, data, error, info } = await axios.get('/api/category/')
        /**     state -   */
        if (success)
        {
            setCategories(data)
        }
        else {
            /**      */
            toast.error(error)
        }
    }

    /** video    */
    const getVideo = async (videoId) =>
    {
        const { success, data, error } = await axios.get(`/api/video/${videoId ? videoId + "/" : ""}`)
        if (success)
        {
            data.author = data.author?._id || ""
            data.category = data?.category?._id
            setFormData(data)
        }
        else {
            toast.error(error)
        }
    }

    /**    video   */
    useEffect(() =>
    {
        getAuthors()
        getCategories()
        /**  id  video   */
        if (videoId)
        {
            getVideo(videoId)
        }
    }, [])

    /**  state -  */
    const handleChange = (event, key) =>
    {
        const value = event.target.value
        setFormData({ ...formData, [key]: value })
    }

    /**   back      */
    const handleSubmit = async (event) =>
    {
        /** submit   refresh    */
        event.preventDefault()

        const body = formData

        /** update   */
        if (videoId)
        {
            const { success, data, error, info } = await axios.put(`/api/video/${videoId}/`, body)
            if (success)
            {
                toast.success(info)
                navigate('/admin/video/')
            }
            else {
                toast.error(error)
            }
            return
        }
        const { success, data, error, info } = await axios.post('/api/video/', body)
        if (success)
        {
            toast.success(info)
            navigate('/admin/video/')
        }
        else {
            toast.error(error)
        }
    }

    return (
        <>
            <h1 className={`page-title`}>Video </h1>
            <div className={`page-content`}>
                <form onSubmit={handleSubmit}>
                    <div>
                        <label htmlFor="url">  :</label>
                        <input
                            type="text"
                            id='url'
                            value={formData.url}
                            onChange={(e) => handleChange(e, 'url')}
                        />
                    </div>
                    <div>
                        <label htmlFor="title">  :</label>
                        <input
                            type="text"
                            id='title'
                            value={formData.title}
                            onChange={(e) => handleChange(e, 'title')}
                        />
                    </div>
                    <div>
                        <label htmlFor="author">Author:</label>
                        <select
                            id="author"
                            value={formData.author}
                            onChange={(e) => handleChange(e, 'author')}
                        >
                            <option value=""></option>
                            {
                                authors.map(
                                    (author, idx) =>
                                    {
                                        return (
                                            <option key={idx} value={author._id}>
                                                {author.nickName}
                                            </option>
                                        )
                                    }
                                )
                            }
                        </select>
                    </div>
                    <div>
                        <label htmlFor="views">     :</label>
                        <input
                            type="number"
                            id='views'
                            value={formData.views}
                            onChange={(e) => handleChange(e, 'views')}
                        />
                    </div>
                    <div>
                        <label htmlFor="category">:</label>
                        <select
                            id="category"
                            value={formData.category}
                            onChange={(e) => handleChange(e, 'category')}
                        >
                            <option value=""></option>
                            {
                                categories.map(
                                    (category, idx) =>
                                    {
                                        return (
                                            <option key={idx} value={category._id}>
                                                {category.name}
                                            </option>
                                        )
                                    }
                                )
                            }
                        </select>
                    </div>
                    <button className='main' type='submit'>
                        
                    </button>
                </form>
            </div>
        </>
    )
}
