import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { toast } from 'react-toastify';

import Button from '../../../../components/main/Button';

import axios from "utils/axios"
import { timeZoneToDateString } from '../../../../utils'

export default function NewsList() {

    /** author   state */
    const [authors, setAuthor] = useState([])

    /** back  author   */
    const getAuthor = async () => {
        const { success, data, error } = await axios.get('/api/author/')
        if (success) {
            /**      state -    */
            setAuthor(data)
        }
        else {
            /**   alert  */
            toast.error(error)
        }
    }

    /**     author   */
    useEffect(() => {
        getAuthor()
    }, [])

    /** Author   */
    const handleDelete = async (id) => {
        const { success, data, info, error } = await axios.delete(`/api/author/${id}/`)
        if (success) {
            /**    alert   */
            toast.success(info)
            getAuthor()
        }
        else {
            /**   alert  */
            toast.error(error)
        }
    }

    return (
        <>
            <h1 className={`page-title`}>Author</h1>
            <div className={`page-content`}>
                <div className="table-title">
                    <Link to={"/admin/author/create/"} className="addBtn">Add</Link>
                </div>
                <table>
                    <thead>
                        <tr>
                            <th>
                                NickName
                            </th>
                            <th>
                                email
                            </th>
                            <th>
                                 
                            </th>
                            <th>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            authors.map(
                                (author, idx) => {
                                    return (
                                        <tr key={idx}>
                                            <td>
                                                {author.nickName}
                                            </td>
                                            <td>
                                                {author.email}
                                            </td>
                                            <td>
                                                {timeZoneToDateString(author.createdAt)}
                                            </td>
                                            <td>
                                                <Link to={`/admin/author/update/${author._id}/`} className="editBtn"></Link>
                                                <Button className="deleteBtn"
                                                    /* style={{
                                                         color: "red"
                                                     }}*/
                                                    onClick={() => handleDelete(author._id)}
                                                    title=""
                                                />
                                            </td>
                                        </tr>
                                    )
                                }
                            )
                        }
                    </tbody>
                </table>
            </div>
        </>
    )
}
