import React, { useEffect, useState, useRef } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { toast } from 'react-toastify'

import axios from 'utils/axios'

export default function NewsForm()
{
    /** form    state */
    const [ formData, setFormData ] = useState({})

    /** update      newsId  */
    const { authorId } = useParams()

    /**     */
    const navigate = useNavigate()

    /** author    */
    const getAuthor = async (authorId) =>
    {
        const { success, data, error } = await axios.get(`/api/author/${authorId ? authorId + "/" : ""}`)
        if (success)
        {
            setFormData(data)
        }
        else {
            toast.error(error)
        }
    }

    /**    author   */
    useEffect(() =>
    {
        /**  id  author   */
        if (authorId)
        {
            getAuthor(authorId)
        }
    }, [])

    /**  state -  */
    const handleChange = (event, key) =>
    {
        const value = event.target.value
        setFormData({ ...formData, [key]: value })
    }

    /**   back      */
    const handleSubmit = async (event) =>
    {
        /** submit   refresh    */
        event.preventDefault()

        const body = formData

        /** update   */
        if (authorId)
        {
            const { success, data, error, info } = await axios.put(`/api/author/${authorId}/`, body)
            if (success)
            {
                toast.success(info)
                navigate('/admin/author/')
            }
            else {
                toast.error(error)
            }
            return
        }
        const { success, data, error, info } = await axios.post('/api/author/', body)
        if (success)
        {
            toast.success(info)
            navigate('/admin/author/')
        }
        else {
            toast.error(error)
        }
    }

    return (
        <>
            <h1 className={`page-title`}>Author </h1>
            <div className={`page-content`}>
                <form onSubmit={handleSubmit}>
                    <div>
                        <label htmlFor="nickName">NickName:</label>
                        <input
                            type="text"
                            id='nickName'
                            value={formData.nickName}
                            onChange={(e) => handleChange(e, 'nickName')}
                        />
                    </div>
                    <div>
                        <label htmlFor="email">email:</label>
                        <input
                            type="email"
                            id='email'
                            value={formData.email}
                            onChange={(e) => handleChange(e, 'email')}
                        />
                    </div>
                    {
                        !authorId
                        &&
                            <div>
                                <label htmlFor="password"> :</label>
                                <input
                                    type="password"
                                    id='password'
                                    value={formData.password}
                                    onChange={(e) => handleChange(e, 'password')}
                                />
                            </div>
                    }
                    <button className='saveBtn' type='submit'>
                        
                    </button>
                </form>
            </div>
        </>
    )
}
