import React, { useEffect, useState, useRef } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { toast } from 'react-toastify'

import axios from 'utils/axios'
import { getImageFormUrl } from 'utils'

export default function NewsForm()
{
    /** form    state */
    const [ formData, setFormData ] = useState({})
    /** author    state */
    const [ authors, setAuthors ] = useState([])
    /**    state */
    const [ categories, setCategories ] = useState([])
    /**    state */
    const [ image, setImage ] = useState(null)
    /** update      newsId  */
    const { podcastId } = useParams()

    /**     */
    const navigate = useNavigate()

    /** author     */
    const getAuthors = async () =>
    {
        const { success, data, error, info } = await axios.get('/api/author/')
        /**    author  state -   */
        if (success)
        {
            setAuthors(data)
        }
        else {
            /**      */
            toast(error)
        }
    }

    /**    */
    const getCategories = async () =>
    {
        const { success, data, error, info } = await axios.get('/api/category/')
        /**     state -   */
        if (success)
        {
            setCategories(data)
        }
        else {
            /**      */
            toast.error(error)
        }
    }

    /** podcast    */
    const getPodcast = async (podcastId) =>
    {
        const { success, data, error } = await axios.get(`/api/podcast/${podcastId ? podcastId + "/" : ""}`)
        if (success)
        {
            data.author = data.author?._id || ""
            data.category = data?.category?._id
            setFormData(data)
        }
        else {
            toast.error(error)
        }
    }

    /**    podcast   */
    useEffect(() =>
    {
        getAuthors()
        getCategories()
        /**  id  podcast   */
        if (podcastId)
        {
            getPodcast(podcastId)
        }
    }, [])

    /**  state -  */
    const handleChange = (event, key) =>
    {
        const value = event.target.value
        setFormData({ ...formData, [key]: value })
    }

    /**   back      */
    const handleSubmit = async (event) =>
    {
        /** submit   refresh    */
        event.preventDefault()

        const reqFormData = new FormData()
        reqFormData.append('title', formData.title)
        reqFormData.append('clock', formData.clock)
        reqFormData.append('text', formData.text)
        reqFormData.append('category', formData.category)
        reqFormData.append('author', formData.author)
        reqFormData.append('url', formData.url)

        if (image)
        {
            Array.from(image.target.files).map((file, idx) => reqFormData.append('image', file))
        }

        const config =
        {
            headers: { 'content-type': 'multipart/form-data' }
        }

        /** update   */
        if (podcastId)
        {
            const { success, data, error, info } = await axios.put(`/api/podcast/${podcastId}/`, reqFormData, config)
            if (success)
            {
                toast.success(info)
                navigate('/admin/podcast/')
            }
            else {
                toast.error(error)
            }
            return
        }
        const { success, data, error, info } = await axios.post('/api/podcast/', reqFormData, config)
        if (success)
        {
            toast.success(info)
            navigate('/admin/podcast/')
        }
        else {
            toast.error(error)
        }
    }

    return (
        <>
            <h1 className={`page-title`}>Podcast </h1>
            <div className={`page-content`}>
                <form onSubmit={handleSubmit}>
                    <div>
                        <label htmlFor="title"> podcast  :</label>
                        <input
                            type="text"
                            id='title'
                            value={formData.title}
                            onChange={(e) => handleChange(e, 'title')}
                        />
                    </div>
                    <div>
                        <label htmlFor="clock">Podcast    ms  :</label>
                        <input
                            type="number"
                            id='clock'
                            value={formData.clock}
                            onChange={(e) => handleChange(e, 'clock')}
                        />
                    </div>
                    <div>
                        <label htmlFor="text">Podcast   :</label>
                        <textarea
                            id="text"
                            value={formData.text}
                            onChange={(e) => handleChange(e, 'text')}
                        >
                        </textarea>
                    </div>
                    <div>
                        <label htmlFor="url">Podcast  :</label>
                        <input
                            type={"text"}
                            id="text"
                            value={formData.url}
                            onChange={(e) => handleChange(e, 'url')}
                        />
                    </div>
                    <div>
                        <label htmlFor="author">Author:</label>
                        <select
                            id="author"
                            value={formData.author}
                            onChange={(e) => handleChange(e, 'author')}
                        >
                            <option value=""></option>
                            {
                                authors.map(
                                    (author, idx) =>
                                    {
                                        return (
                                            <option key={idx} value={author._id}>
                                                {author.nickName}
                                            </option>
                                        )
                                    }
                                )
                            }
                        </select>
                    </div>
                    <div>
                        <label htmlFor="category">:</label>
                        <select
                            id="category"
                            value={formData.category}
                            onChange={(e) => handleChange(e, 'category')}
                        >
                            <option value=""></option>
                            {
                                categories.map(
                                    (category, idx) =>
                                    {
                                        return (
                                            <option key={idx} value={category._id}>
                                                {category.name}
                                            </option>
                                        )
                                    }
                                )
                            }
                        </select>
                    </div>
                    <div>
                        <label htmlFor="image">Podcast  thubmnail :</label>
                        <input
                            id="image"
                            type={"file"}
                            onChange={setImage}
                            multiple={false}
                        >
                        </input>
                    </div>
                    <button className='main' type='submit'>
                        
                    </button>
                </form>
            </div>
        </>
    )
}
