import axios from 'axios'

/**   axios */
const instance = axios.create(
    {
        baseURL: process.env.REACT_APP_SERVER_URL,
        withCredentials: true,
    }
)
instance.interceptors.response.use(
    function(rsp)
    {
        /** object   data  */
        const data = rsp.data
        return data
    },
    function(err)
    {
        /** object   data   */
        const error = err?.response?.data
        return error
    }
)

export default instance
