import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { toast } from 'react-toastify';

import Button from '../../../../components/main/Button';

import axios from "utils/axios"
import { timeZoneToDateString } from '../../../../utils'

export default function NewsList() {

    /**   state */
    const [newsList, setNews] = useState([])

    /** back     */
    const getNews = async () => {
        const { success, data, error } = await axios.get('/api/news/')
        if (success) {
            /**      state -    */
            setNews(data)
        }
        else {
            /**   alert  */
            toast.error(error)
        }
    }

    /**        */
    useEffect(() => {
        getNews()
    }, [])

    /**    */
    const handleDelete = async (id) => {
        const { success, data, info, error } = await axios.delete(`/api/news/${id}/`)
        if (success) {
            /**    alert   */
            toast.success(info)
            getNews()
        }
        else {
            /**   alert  */
            toast.error(error)
        }
    }

    return (
        <>
            <h1 className={`page-title`}>NEWS</h1>

            <div className={`page-content`}>
                <div className="table-title">
                    <Link to={"/admin/news/create/"} className="addBtn">Add</Link>
                </div>
                <table>
                    <thead>
                        <tr>
                            <th>
                                
                            </th>
                            <th>
                                
                            </th>
                            <th>
                                
                            </th>
                            <th>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            newsList.map(
                                (news, idx) => {
                                    return (
                                        <tr key={idx}>
                                            <td>
                                                {news.title}
                                            </td>
                                            <td>
                                                {news.author?.nickName || ""}
                                            </td>
                                            <td>
                                                {timeZoneToDateString(news.createdAt)}
                                            </td>
                                            <td>
                                                <Link to={`/admin/news/update/${news._id}/`} className="editBtn"></Link>
                                                <Button className="deleteBtn"
                                                    onClick={() => handleDelete(news._id)}
                                                    title=""
                                                />
                                            </td>
                                        </tr>
                                    )
                                }
                            )
                        }
                    </tbody>
                </table>
            </div>
        </>
    )
}
