import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { toast } from 'react-toastify';

import Button from '../../../../components/main/Button';

import axios from "utils/axios"
import { timeZoneToDateString } from '../../../../utils'

export default function CategoryList() {

    /** category   state */
    const [categories, setCategories] = useState([])

    /** back  category   */
    const getCategory = async () => {
        const { success, data, error } = await axios.get('/api/category/')
        if (success) {
            /**      state -    */
            setCategories(data)
        }
        else {
            /**   alert  */
            toast.error(error)
        }
    }

    /**     category   */
    useEffect(() => {
        getCategory()
    }, [])

    /** category   */
    const handleDelete = async (id) => {
        const { success, data, info, error } = await axios.delete(`/api/category/${id}/`)
        if (success) {
            /**    alert   */
            toast.success(info)
            getCategory()
        }
        else {
            /**   alert  */
            toast.error(error)
        }
    }

    return (
        <>
            <h1 className={`page-title`}>Category</h1>
            <div className={`page-content`}>
                <div className="table-title">
                    <Link to={"/admin/news/create/"} className="addBtn">Add</Link>
                </div>
                <table>
                    <thead>
                        <tr>
                            <th>
                                
                            </th>
                            <th>
                                 
                            </th>
                            <th>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            categories.map(
                                (category, idx) => {
                                    return (
                                        <tr key={idx}>
                                            <td>
                                                {category.name}
                                            </td>
                                            <td>
                                                {timeZoneToDateString(category.createdAt)}
                                            </td>
                                            <td>
                                                <Link to={`/admin/category/update/${category._id}/`} className="editBtn"></Link>
                                                <Button className="deleteBtn"
                                                    /* style={{
                                                         color: "red"
                                                     }}*/
                                                    onClick={() => handleDelete(category._id)}
                                                    title=""
                                                />
                                            </td>
                                            <div className="hr-line-dashed"></div>
                                        </tr>
                                    )
                                }
                            )
                        }
                    </tbody>
                </table>
            </div>
        </>
    )
}
