import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { toast } from 'react-toastify';

import Button from '../../../../components/main/Button';

import axios from "utils/axios"
import { timeZoneToDateString } from '../../../../utils'

export default function VideoList() {

    /** video  state */
    const [videos, setVideos] = useState([])

    /** back  video   */
    const getVideos = async () => {
        const { success, data, error } = await axios.get('/api/video/')
        if (success) {
            /**      state -    */
            setVideos(data)
        }
        else {
            /**   alert  */
            toast.error(error)
        }
    }

    /**        */
    useEffect(() => {
        getVideos()
    }, [])

    /** video   */
    const handleDelete = async (id) => {
        const { success, data, info, error } = await axios.delete(`/api/video/${id}/`)
        if (success) {
            /**    alert   */
            toast.success(info)
            getVideos()
        }
        else {
            /**   alert  */
            toast.error(error)
        }
    }

    return (
        <>
            <h1 className={`page-title`}>VIDEO</h1>
            <div className={`page-content`}>
                <div className="table-title">
                    <Link to={"/admin/news/create/"} className="addBtn">Add</Link>
                </div>
                <table>
                    <thead>
                        <tr>
                            <th>
                                
                            </th>
                            <th>
                                
                            </th>
                            <th>
                                Category
                            </th>
                            <th>
                                
                            </th>
                            <th>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            videos.map(
                                (video, idx) => {
                                    return (
                                        <tr key={idx}>
                                            <td>
                                                {video.title}
                                            </td>
                                            <td>
                                                {video.author?.nickName}
                                            </td>
                                            <td>
                                                {video.category?.name || ""}
                                            </td>
                                            <td>
                                                {timeZoneToDateString(video.createdAt)}
                                            </td>
                                            <td>
                                                <Link to={`/admin/video/update/${video._id}/`} className="editBtn"></Link>
                                                <Button className="deleteBtn"
                                                    onClick={() => handleDelete(video._id)}
                                                    title=""
                                                />
                                            </td>
                                        </tr>
                                    )
                                }
                            )
                        }
                    </tbody>
                </table>
            </div>
        </>
    )
}
