import React, { useEffect, useState, useRef } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { toast } from 'react-toastify'

import axios from 'utils/axios'

export default function NewsForm()
{
    /** form    state */
    const [ formData, setFormData ] = useState({})

    /** update      newsId  */
    const { categoryId } = useParams()

    /**     */
    const navigate = useNavigate()

    /** category    */
    const getCategories = async (categoryId) =>
    {
        const { success, data, error } = await axios.get(`/api/category/${categoryId ? categoryId + "/" : ""}`)
        if (success)
        {
            setFormData(data)
        }
        else {
            toast.error(error)
        }
    }

    /**    category   */
    useEffect(() =>
    {
        /**  id  category   */
        if (categoryId)
        {
            getCategories(categoryId)
        }
    }, [])

    /**  state -  */
    const handleChange = (event, key) =>
    {
        const value = event.target.value
        setFormData({ ...formData, [key]: value })
    }

    /**   back      */
    const handleSubmit = async (event) =>
    {
        /** submit   refresh    */
        event.preventDefault()

        const body = formData

        /** update   */
        if (categoryId)
        {
            const { success, data, error, info } = await axios.put(`/api/category/${categoryId}/`, body)
            if (success)
            {
                toast.success(info)
                navigate('/admin/category/')
            }
            else {
                toast.error(error)
            }
            return
        }
        const { success, data, error, info } = await axios.post('/api/category/', body)
        if (success)
        {
            toast.success(info)
            navigate('/admin/category/')
        }
        else {
            toast.error(error)
        }
    }

    return (
        <>
            <h1 className={`page-title`}>category </h1>
            <div className={`page-content`}>
                <form onSubmit={handleSubmit}>
                    <div>
                        <label htmlFor="name">:</label>
                        <input
                            type="text"
                            id='name'
                            value={formData.name}
                            onChange={(e) => handleChange(e, 'name')}
                        />
                    </div>
                    <button className='main' type='submit'>
                        
                    </button>
                </form>
            </div>
        </>
    )
}
