import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { toast } from 'react-toastify';

import Button from '../../../../components/main/Button';

import axios from "utils/axios"
import { timeZoneToDateString } from '../../../../utils'

export default function PodcastList() {
    /** podcast  state */
    const [podcasts, setPodcasts] = useState([])

    /** back  podcast   */
    const getPodcasts = async () => {
        const { success, data, error } = await axios.get('/api/podcast/')
        if (success) {
            /**      state -    */
            setPodcasts(data)
        }
        else {
            /**   alert  */
            toast.error(error)
        }
    }

    /**     podcast   */
    useEffect(() => {
        getPodcasts()
    }, [])

    /**    */
    const handleDelete = async (id) => {
        const { success, data, info, error } = await axios.delete(`/api/podcast/${id}/`)
        if (success) {
            /**    alert   */
            toast.success(info)
            getPodcasts()
        }
        else {
            /**   alert  */
            toast.error(error)
        }
    }

    return (
        <>
            <h1 className={`page-title`}>Podcast</h1>
            <div className={`page-content`}>
                <div className="table-title">
                    <Link to={"/admin/news/create/"} className="addBtn">Add</Link>
                </div>
                <table>
                    <thead>
                        <tr>
                            <th>
                                
                            </th>
                            <th>
                                
                            </th>
                            <th>
                                Category
                            </th>
                            <th>
                                
                            </th>
                            <th>
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            podcasts.map(
                                (podcast, idx) => {
                                    return (
                                        <tr key={idx}>
                                            <td>
                                                {podcast.title}
                                            </td>
                                            <td>
                                                {podcast.author?.nickName || ""}
                                            </td>
                                            <td>
                                                {podcast.category?.name || ""}
                                            </td>
                                            <td>
                                                {timeZoneToDateString(podcast.createdAt)}
                                            </td>
                                            <td>
                                                <Link to={`/admin/podcast/update/${podcast._id}/`} className="editBtn"></Link>
                                                <Button className="deleteBtn"
                                                    onClick={() => handleDelete(podcast._id)}
                                                    title=""
                                                />
                                            </td>
                                        </tr>
                                    )
                                }
                            )
                        }
                    </tbody>
                </table>
            </div>
        </>
    )
}
